.section-title {
  font-size: 32px;
  font-weight: 500;
  line-height: 100%;
  letter-spacing: -1.5px;
  text-align: center;
  margin-bottom: 48px;

  @include onTablet {
    font-size: 48px;
  }

  @include onDesktop {
    font-size: 64px;
  }
}

.section-title--align-left {
  text-align: left;
  white-space: nowrap;
}


